.footer {
  background: var(--color-primary);
  padding: 3rem 0;
  text-align: center;
  font-size: 0.9rem;
  margin-top: 7rem;

  a {
    color: var(--color-bg);
  }

  &__logo {
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;
  }
  .permalinks {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    margin: 0 auto 3rem;
  }
  &__socials {
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 4rem;
    font-size: 1.5rem;

    a {
      background: var(--color-bg);
      color: var(--color-white);
      padding: 0.7rem;
      border-radius: 0.7rem;
      display: flex;
      border: 1px solid transparent;

      &:hover {
        border-color: var(--color-bg);
        color: var(--color-bg);
        background: transparent;
      }
    }
  }
  &__copyright {
    margin-bottom: 4rem;
    color: var(--color-bg);
  }
}

/* Media Quieres */


@media screen and (max-width: 600px) {
    .footer{ 
        .permalinks{ 
            flex-direction: column;
            gap: 1.4rem;
        }

        &__socials {
            margin-bottom: 1.2rem;
        }
    }
}
