.experience {
  &__container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;

    & > div {
      background: var(--color-bg-variant);
      padding: 2.4rem 5rem;
      border-radius: 2rem;
      border: 1px solid transparent;
      transition: var(--transition);

      &:hover {
        background: transparent;
        border-color: var(--color-primary-variant);
        cursor: default;
      }

      h3 {
        text-align: center;
        margin-bottom: 2rem;
        color: var(--color-primary);
      }
    }
  }

  &__content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    row-gap: 2rem;
  }
  &__detalis {
    display: flex;
    gap: 1rem;
    align-items: center;
    &-icon {
      color: var(--color-primary);
    }
  }
}

/* Media Quieres */
@media screen and (max-width: 1024px) {
  .experience {
    &__container {
      grid-template-columns: repeat(1, 1fr);

      & > div {
        width: 80%;
        padding: 2rem;
        margin: 0 auto;
      }
    }
    &__content { 
        padding: 1rem;
    }
  }
}

@media screen and (max-width: 600px) {
  .experience {
    &__container { 
        gap: 1rem;


        & > div { 
            width: 100%;
            padding: 2rem 1rem;
        }
    }
  }
}
